import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State, Getter } from 'vuex-class';

import Api from '~/api';
import { ToastMsg } from '~/entity/toast';
import { IAddFriendItem } from '~/entity/friend/Ifriend';

import Utils from '~/utils';
import { IFriendListParams } from '~/entity/route_params';

@Component({
    // mixins: [mixin]
})
export default class FriendIndexComp extends Vue {
    /* 各种分类的好友 */
    // @State(state => state.user.singleMaleFriendsCount)
    // singleMaleFriendsCount;
    @Getter('singleMaleFriendsCount')
    singleMaleFriendsCount;

    // @State(state => state.user.singleFemaleFriendsCount)
    // singleFemaleFriendsCount;
    @Getter('singleFemaleFriendsCount')
    singleFemaleFriendsCount;

    // @State(state => state.user.nonSingleFriendsCount)
    // nonSingleFriendsCount;
    @Getter('nonSingleFriendsCount')
    nonSingleFriendsCount;

    /* 刷新控件 */
    refreshingFriends = false;

    friendsContainer: HTMLElement = null;

    /******** */
    hasNewFriend = false;

    @Action('showToast')
    showToast;

    @Action('updateTabValue')
    updateTabValue;

    $refs: {
        friendsContainer: any
    };

    // beforeMount() {
    //     debugger;
    //     console.log(this.$vnode);
    // }

    mounted() {
        this.friendsContainer = this.$refs.friendsContainer;
    }

    activated() {
        this.updateTabValue('friends');
        this.refreshFriend();
    }

    async refreshFriend() {
        this.refreshingFriends = true;
        try {
            const res = await Api.getAddFriendList();

            // 检查是否有新好友申请
            this.checkNewFriends(res.body.obj);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.refreshingFriends = false;
        }
    }

    /**
     * 检查是否新的好友
     * @param friends 
     */
    checkNewFriends(friends: IAddFriendItem[]) {
        this.hasNewFriend = false;
        for (const friend of friends) {
            if (friend.recv === 0) {
                this.hasNewFriend = true;
                break;
            }
        }
    }

    toSearchAddPage() {
        this['$router'].push({
            name: 'searchAddFriends'
        });
    }
    toFriendHistoryPage() {
        this['$router'].push({
            name: 'friendHistory'
        });
    }

    toFriendListPage(action: string) {
        const params: IFriendListParams = {
            action
        };
        this['$router'].push({
            name: 'friendList',
            params
        });
    }
}



